export const getComments = async (parentId = null) => {
  return [
    {
      id: Math.random().toString(36).substr(2, 9),
      body: "First Comment",
      username: "John",
      parentId: null,
      createdAt: new Date().toISOString(),
    },
  ];
};

export const createComment = async (text, name, count) => {
  return {
    id: Math.random().toString(36).substr(2, 9),
    body: text,
    username: name,

    parentId: null,
    createdAt: new Date().toISOString(),
  };
};

export const replyComment = async (text, name, parentId) => {
  return {
    id: Math.random().toString(36).substr(2, 9),
    body: text,
    username: name,

    parentId: null,
    createdAt: new Date().toISOString(),
  };
};

export const updateComment = async (text) => {
  return { text };
};

export const deleteComment = async () => {
  return {};
};
