import { Link } from "react-router-dom";

export const Columns = [
  {
    id: "Rarity",
    Header: "Rarity",
    accessor: "stars",
    Cell: (cellProps) => (
      <div className="hero_column_0 hero_clmn_rarity ">
        <span className="fw-bold">{cellProps.row.original.stars}</span>
        <img src="../Images/Misc/Stars_TypeIcons/rarity.png" alt="Rarity" />
        <img
          src={cellProps.row.original.typeImg}
          width="30"
          alt={cellProps.row.original.type}
        />
      </div>
    ),
  },

  {
    id: "Esper",
    Header: "Esper",
    accessor: "esperName",
    Cell: (tableProps) => (
      <Link
        to={tableProps.row.original.esperName}
        style={{ textDecoration: "none", color: "black" }}>
        <div
          className="charimage"
          style={{ display: "flex", flexDirection: "column" }}>
          <img
            style={{ margin: "auto" }}
            src={tableProps.row.original.charImg}
            width={65}
            alt="Esper"
          />
          <span style={{ fontWeight: "bold" }}>
            {tableProps.row.original.esperName}
          </span>
          {tableProps.row.original.deity}
        </div>
      </Link>
    ),
  },

  {
    id: "Overall",
    Header: "Overall",
    accessor: "hero_column_overall",
    Cell: (tableProps) => {
      return (
        <div>
          {(tableProps.row.original.hero_column_story +
            tableProps.row.original.hero_column_kronos +
            tableProps.row.original.hero_column_apep +
            tableProps.row.original.hero_column_fafnir +
            tableProps.row.original.hero_column_shdwfire +
            tableProps.row.original.hero_column_shdwgale +
            tableProps.row.original.hero_column_shdwstream +
            tableProps.row.original.hero_column_sonicmir +
            tableProps.row.original.hero_column_tt +
            tableProps.row.original.hero_column_pw) /
            10}
        </div>
      );
    },
  },
  {
    id: "Story",
    Header: "Story",
    accessor: "hero_column_story",
  },

  {
    id: "Kronos",
    Header: "Kronos",
    accessor: "hero_column_kronos",
  },

  {
    id: "Apep",
    Header: "Apep",
    accessor: "hero_column_apep",
  },

  {
    id: "Fafnir",
    Header: "Fafnir",
    accessor: "hero_column_fafnir",
  },

  {
    id: "Shadowfire",
    Header: "Shadowfire",
    accessor: "hero_column_shdwfire",
  },

  {
    id: "Shadowgale",
    Header: "Shadowgale",
    accessor: "hero_column_shdwgale",
  },
  {
    id: "Shadowstream",
    Header: "Shadowstream",
    accessor: "hero_column_shdwstream",
  },

  {
    id: "Sonic Miracle",
    Header: "Sonic Miracle",
    accessor: "hero_column_sonicmir",
  },

  {
    id: "Temporal Tower",
    Header: "Temporal Tower",
    accessor: "hero_column_tt",
  },

  {
    id: "Point War",
    Header: "Point War",
    accessor: "hero_column_pw",
  },
];
