import React from "react";

import { TwitterTimelineEmbed } from "react-twitter-embed";
import { Timeline } from "react-twitter-widgets";
import "./HomePage.css";

export const Twitter = () => {
  return (
    <section className="News_wrapper">
      <div className="container">
        <div className="row homeTitle mt-3">
          <h4>News</h4>
        </div>
        <div className="twitterContainer d-flex flex-wrap">
          <div className="twitter_embed">
            <Timeline
              dataSource={{
                sourceType: "profile",
                screenName: "dislyte",
              }}
              options={{
                height: "500",
                width: "auto",
              }}
            />
          </div>
          <div>
            <img
              className="twitterEsperImg"
              src={"./Images/Character/Embla_Ymir/Embla_pose_sprite.webp"}
              alt="Embla"
            />
          </div>
        </div>
      </div>
    </section>
  );
};
