import React, { useState, useEffect } from "react";

import "../../index.css";

const CommentForm = ({
  handleSubmit,
  submitLabel,
  hasCancelButton = false,
  initialText = "",
  initialName = "",
  handleCancel,
  display = "block",
}) => {
  const [text, setText] = useState(initialText);
  const [name, setName] = useState(initialName);
  const isTextareaDisabled = name.length === 0 || text.length === 0;

  const onSubmit = (e) => {
    e.preventDefault();
    handleSubmit(text, name);
    setName("");
    setText("");
  };

  return (
    <div>
      <form onSubmit={onSubmit} className="container w-50 ">
        <div>
          <input
            className="comment-form-namearea"
            type="text"
            id="name"
            name="username"
            placeholder=" Username"
            value={name}
            onChange={(e) => setName(e.target.value)}
            style={{ display: `${display}` }}
          />

          <textarea
            className="comment-form-textarea"
            placeholder=" Comment"
            name="body"
            type="text"
            value={text}
            onChange={(e) => setText(e.target.value)}
          />
        </div>

        <button className="comment-form-button">{submitLabel}</button>

        {hasCancelButton && (
          <button
            type="button"
            className="comment-form-button comment-form-cancel-button"
            onClick={handleCancel}>
            Cancel
          </button>
        )}
      </form>
    </div>
  );
};

export default CommentForm;
